.hd html {
  line-height: 1.15; /* 1 */
  -webkit-text-size-adjust: 100%; /* 2 */
}
.hd body {
  margin: 0;
}
.hd main {
  display: block;
}
.hd h1 {
  font-size: 2em;
  margin: 0.67em 0;
}
.hd hr {
  box-sizing: content-box; /* 1 */
  height: 0; /* 1 */
  overflow: visible; /* 2 */
}
.hd pre {
  font-family: monospace, monospace; /* 1 */
  font-size: 1em; /* 2 */
}
.hd a {
  background-color: transparent;
}
.hd abbr[title] {
  border-bottom: none; /* 1 */
  text-decoration: underline; /* 2 */
  text-decoration: underline dotted; /* 2 */
}
.hd b,
.hd strong {
  font-weight: 800;
}
.hd code,
.hd kbd,
.hd samp {
  font-family: monospace, monospace; /* 1 */
  font-size: 1em; /* 2 */
}
.hd small {
  font-size: 80%;
}
.hd sub,
.hd sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}
.hd sub {
  bottom: -0.25em;
}
.hd sup {
  top: -0.5em;
}
.hd img {
  border-style: none;
}
.hd button,
.hd input,
.hd optgroup,
.hd select,
.hd textarea {
  font-family: inherit; /* 1 */
  font-size: 100%; /* 1 */
  line-height: 1.15; /* 1 */
  margin: 0; /* 2 */
}
.hd button,
.hd input { /* 1 */
  overflow: visible;
}
.hd button,
.hd select { /* 1 */
  text-transform: none;
}
.hd button,
.hd [type=button],
.hd [type=reset],
.hd [type=submit] {
  -webkit-appearance: button;
}
.hd button::-moz-focus-inner,
.hd [type=button]::-moz-focus-inner,
.hd [type=reset]::-moz-focus-inner,
.hd [type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}
.hd button:-moz-focusring,
.hd [type=button]:-moz-focusring,
.hd [type=reset]:-moz-focusring,
.hd [type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}
.hd fieldset {
  padding: 0.35em 0.75em 0.625em;
}
.hd legend {
  box-sizing: border-box; /* 1 */
  color: inherit; /* 2 */
  display: table; /* 1 */
  max-width: 100%; /* 1 */
  padding: 0; /* 3 */
  white-space: normal; /* 1 */
}
.hd progress {
  vertical-align: baseline;
}
.hd textarea {
  overflow: auto;
}
.hd [type=checkbox],
.hd [type=radio] {
  box-sizing: border-box; /* 1 */
  padding: 0; /* 2 */
}
.hd [type=number]::-webkit-inner-spin-button,
.hd [type=number]::-webkit-outer-spin-button {
  height: auto;
}
.hd [type=search] {
  -webkit-appearance: textfield; /* 1 */
  outline-offset: -2px; /* 2 */
}
.hd [type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}
.hd ::-webkit-file-upload-button {
  -webkit-appearance: button; /* 1 */
  font: inherit; /* 2 */
}
.hd details {
  display: block;
}
.hd summary {
  display: list-item;
}
.hd template {
  display: none;
}
.hd [hidden] {
  display: none;
}

.hd-app {
  color: #141B38;
  font-family: "Lexend Deca", sans-serif;
  font-size: 14px;
  line-height: 22px;
