  fill: #141B38;
  color: #141B38;
  background-color: #F3F4F5;
}
.hd-btn--default:hover, .hd-btn--default:focus, .hd-btn--default[data-state=active] {
  background-color: #E5E7E9 !important;
  transition: all 320ms ease-in-out 0s;
}
.hd-btn__detail {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}
.hd-btn__icon {
  display: block;
  width: 16px;
  height: 16px;
}
.hd-btn__loader {
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.hd-btn:hover, .hd-btn:focus, .hd-btn[data-state=active] {
  background-color: #1A82EA;
  transition: all 320ms ease-in-out 0s;
}

a.hd-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 14px;
  color: #FFF;
}
a.hd-btn:hover, a.hd-btn:focus {
  color: #FFF;
}

.hd-btn-icon {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  min-width: 40px;
  max-width: 40px;
  min-height: 40px;
  max-height: 40px;
  fill: #141B38;
  color: #141B38;
  background-color: #F0F0F0;
  border: 0;
}
.hd-btn-icon svg {
  width: 18px;
  height: 18px;
}
.hd-btn-icon[data-visibility=hidden] {
  display: none;
}
.hd-btn-icon[data-type=icon] {
  display: block;
  min-width: 20px;
  max-width: 20px;
  min-height: 20px;
  max-height: 20px;
  background-color: transparent;
}
.hd-btn-icon[data-type=icon] svg {
  width: 20px;
  height: 20px;
}
.hd-btn-icon[data-type=button]:hover, .hd-btn-icon[data-type=button]:focus, .hd-btn-icon[data-type=button][data-state=active] {
  background-color: #E5E7E9;
  transition: all 320ms ease-in-out 0s;
}
.hd-btn-icon[data-state=default] .hd-btn-nav__icon--active {
  display: none;
}
.hd-btn-icon[data-type=button][data-state=loading] .hd-loader {
  display: block;
}
.hd-btn-icon[data-type=button][data-state=loading] svg {
  display: none;
}
.hd-btn-icon[data-state=active] .hd-btn-nav__icon--default {
  display: none;
}
.hd-btn-icon--square {
  border-radius: 2px;
}
.hd-btn-icon--circle {
  border-radius: 100%;
}
.hd-btn-icon .hd-loader {
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

a.hd-btn-icon:hover, a.hd-btn-icon:focus {
  color: #141B38;
}

.hd-home {
  padding: 25px 20px;
}
.hd-home__container {
  margin: 0 auto;
  width: 100%;
  max-width: 1200px;
}
.hd-home__banner {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 50px;
  padding: 20px;
  width: 100%;
  height: 420px;
  background-color: #E8F1FB;
  border-radius: 4px;
}
.hd-home__developer {
  font-size: 14px;
  font-weight: 700;
  letter-spacing: 1px;
  color: #0071F2;
}
.hd-home__developer a {
  font-size: 14px;
  color: #0071F2;
}
.hd-home__developer a:hover, .hd-home__developer a:focus {
  color: #034DA2;
}
.hd-home__title {
  margin-bottom: 10px !important;
  font-size: 80px;
  line-height: 96px;
}
.hd-home__description {
  width: 100%;
  max-width: 675px;
  font-size: 16px !important;
  line-height: 24px !important;
}
.hd-home-plugins {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
}
.hd-home-plugin {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 25px;
  background-color: #FFF;
  border-radius: 2px;
}
.hd-home-plugin__header {
  margin-bottom: 20px;
}
.hd-home-plugin__header .hd-home-col {
  display: flex;
  gap: 15px;
}
.hd-home-plugin__content {
  margin-bottom: 40px;
}
.hd-home-plugin__icon {
  width: 80px;
  height: 80px;
  border-radius: 2px;
}
.hd-home-plugin__name {
  margin-bottom: 5px !important;
}
.hd-home-plugin__status {
  display: flex;
  align-items: center;
  gap: 5px;
}
.hd-home-plugin__status[data-state=installed] {
  color: #228B22;
}
.hd-home-plugin__status[data-state=not-installed] {
  color: #525252;
}
.hd-home-plugin__status svg {
  width: 16px;
  height: 16px;
}
.hd-home-plugin__tools {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  column-gap: 10px;
  row-gap: 5px;
}
.hd-home-plugin__tool {
  display: flex;
  align-items: center;
  gap: 8px;
  color: #525252 !important;
}
.hd-home-plugin__tool:hover, .hd-home-plugin__tool:focus {
  color: #0071F2 !important;
}
.hd-home-plugin__tool span {
  font-weight: 600;
}
.hd-home-plugin__footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
}
@media (max-width: 1040px) {
  .hd-home-plugins {
    grid-template-columns: 1fr;
  }
}
@media (max-width: 680px) {
  .hd-home__title {
    font-size: 60px;
    line-height: 72px;
  }
}
@media (max-width: 550px) {
  .hd-home__banner {
    height: 360px;
  }
  .hd-home__title {
    font-size: 50px;
    line-height: 60px;
  }
  .hd-home-plugin__footer {
    align-items: flex-start;
    flex-direction: column;
  }
  .hd-home-plugin__footer .hd-btn {
    width: 100%;
  }
  .hd-home-plugin__actions {
    width: 100%;
  }
}
@media (max-width: 450px) {
  .hd-home__title {
    font-size: 40px;
    line-height: 48px;
  }
}